@use "common";

.sc {
  padding: 10rem 0;

  &.bg {
    background: #f9f9f9;
  }

  h2 {
    font-size: 2.5rem;
    font-weight: 700;
    margin: 0 0 2rem 0;
    text-align: center;

    &::after {
      content: "";
      display: block;
      width: 6rem;
      height: 0.1rem;
      background: common.$mainColor;
      margin: 1rem auto;
    }
  }

  p {
    font-size: 1.5rem;
    font-weight: 300;
    margin: 0 0 5rem 0;
    text-align: center;
  }
}

.sce {
  padding: 10rem 0;

  &.bg {
    background: #f9f9f9;
  }

  h2 {
    font-size: 2.5rem;
    font-weight: 700;
    margin: 0 0 2rem 0;
    text-align: center;

    &::after {
      content: "";
      display: block;
      width: 6rem;
      height: 0.1rem;
      background: common.$mainColor;
      margin: 1rem auto;
    }
  }

  p {
    font-size: 1.5rem;
    font-weight: 300;
    margin: 0 0 5rem 0;
    text-align: center;
  }

  .inner {
    width: 120rem;

    figure {
      padding: 0 1.5rem;
    }

    .box {
      background: #fff;
      padding: 2rem 1rem;
      border: 1px solid #ddd;
      margin: 0 0 2rem 0;
    }

    .name {
      font-weight: 600;
      margin: 0 0 1rem 0;
      min-height: 3rem;
    }

    .des {
      font-size: 1.4rem;
      margin: 0 0 1rem 0;
      min-height: 10rem;
    }

    .price {
      font-size: 1.5rem;
      font-weight: 500;

      span {
        color: tomato;
      }
    }
  }
}

.slide_ {
  .inner {
    position: relative;
  }

  .slick-arrow {
    position: absolute;
    top: 50%;
    left: 50%;

    font-size: 0;
    width: 5rem;
    height: 5rem;
    border: none;
    background: transparent;
    border-left: 1px solid #ddd;
    border-top: 1px solid #ddd;
    z-index: 999;
  }

  .slick-prev {
    transform: translate(-50%, -50%) rotate(-45deg);
    margin: 0 0 0 -70rem;
  }

  .slick-next {
    transform: translate(-50%, -50%) rotate(135deg);
    margin: 0 0 0 70rem;
  }

  .slick-dots {
    font-size: 0;
    text-align: center;
    margin: 4rem 0 0 0;

    li {
      display: inline-block;

      button {
        border: none;
        background: transparent;
        width: 3rem;
        height: 0.4rem;
        background: common.$mainColor;
        margin: 0 0.4rem;
      }

      &.slick-active button {
        background: tomato;
      }
    }
  }
}

.bp_tab {
  display: flex;
  justify-content: center;
  margin-bottom: 5rem;

  li {
    padding: 1rem 4rem;
    font-size: 1.5rem;
    background: #f7f7f7;
    text-transform: uppercase;
    cursor: pointer;
  }

  li ~ li {
    border-left: 3px solid #fff;
  }
}
